.End {
    color: rgb(255, 255, 255);
    margin-top: 20px;
    display: flex;
    position: absolute;
    width: 100%;
    padding-left: 60px;
    padding-right: 60px;
}

.End>div {
    height: 390px;
    width: 25%;
}

.logo {
    width: 100%;
    height: 100%;
    text-align: center;
}

.logo-image {
    margin-top: 40px;
    margin-left: 40px;
    width: 50%;
    height: auto;
}

/* Style all font awesome icons */
.logo .fa {
    margin-left: 10px;
    padding: 10px;
    font-size: 30px;
    width: 50px;
    text-align: center;
    text-decoration: none;
    border-radius: 50%;
}

/* Add a hover effect if you want */
.fa:hover {
    opacity: 0.7;
}

/* Set a specific color for each brand */

/* Facebook */
.fa-facebook {
    background: #3B5998;
    color: white;
}

/* Twitter */
.fa-twitter {
    background: #55ACEE;
    color: white;
}

/* Instagram */
.fa-instagram {
    color: white;
    background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%,#d6249f 60%,#285AEB 90%);
}

.follow-us{
    color: rgb(255, 255, 255);
    font-size: 30px;
    text-align: left;
    margin-left: 32%;
    margin-top: 10px;
}

.logo .fa:first-child{
    margin-left: 30px;
}

p.section-heading{
    font-weight: 800;
    font-size: 30px;
}
p.section-content{
    font-weight: 500;
    font-size: 15px;
}
.color-blue{
    background-color: #2D8AB3;
    color: white;
}
.button-width{
    width: 100%;
    
}

.End ul{
    list-style: none;
    font-weight: 500;
    font-size: 15px;
    padding: 0 !important;
}

.End a{
    text-decoration: none;
    color: white;
}

.End h1{
    font-weight: 800;
    font-size: 30px;
}

.End li{
    margin-top: 15px;
}

.End ul .fa{
    margin-right: 10px;
}

.End .quick-links li:hover{
    transform: scale(1.1);
    transition: 0.2s ease;
}

.End {
    text-shadow: 2px 7px 5px rgba(0,0,0,0.3), 0px -4px 10px rgba(0, 0, 0, 0.3) !important;
}