.contact-section {
    position: relative;
    /* Add position: relative to the section */
}

.background {
    position: absolute;
    top: 0;
    left: 0;
    height: 600px;
    width: 100%;
    background-color: #020202;
    z-index: -1;
    /* Add a negative z-index to move it behind other elements */
}

.contact-page {
    position: relative;
    /* Add position: relative to the contact-page div */
    z-index: 1;
    /* Add a higher z-index to place it in front of the background */
}

.w-100 {
    border-radius: 20px !important;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px !important;
    overflow: hidden !important;
    height: 500px;
    width: 70% !important;
    margin-top: 80px;
}
input:focus, textarea:focus{
    box-shadow: rgba(185, 195, 199, 0.35) 0px 5px 15px !important;
}
input, textarea{
    box-shadow: rgba(185, 195, 199, 0.35) 0px 5px 15px !important;
    resize: none;
}
