.home-section {
    background-image: url('../assets/Home.jpg');
    background-size: cover;
    height: 100vh;
    width: 100%;
    clip-path: polygon(0 0, 100% 0, 100% calc(100% - 50px), 49% 100%, 0 calc(100% - 50px));
}

.HomeText {
    margin-top: 250px;
    margin-left: 100px;
    color: #ffffff;
}

.HomeText h1 {
    font-weight: bolder;
    font-size: 70px;
}

.HomeText h2 {
    font-size: 34px;
}

.HomeText p {
    padding: 10px;
    font-size: 20px;
    font-weight: bold;
}

.HomeText button {
    background-color: #030303e7;
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    border-radius: 10px;
    font-weight: bold;
}

.HomeText button:hover {
    box-shadow: #ffffff 0.5px 0.5px 10px 0.5px;
}

.down-arrow {
    position: absolute;
    top: calc(100vh - 80px);
    left: calc(50% - 14px);
    width: 3.5px;
    background-color: rgb(255, 255, 255);
    height: 30px;
    border: 2px solid;
    border-radius: 2px;
    animation: jumpInfinite 1.5s infinite;
    color: rgb(255, 255, 255);
    box-shadow: #ffffff 0.5px 0.5px 10px 0.5px;
}

.down-arrow:after {
    content: " ";
    position: absolute;
    top: 12px;
    left: -7.15px;
    width: 16px;
    height: 16px;
    border-bottom: 4px solid;
    border-right: 4px solid;
    border-radius: 4px;
    transform: rotateZ(45deg);
}

@keyframes jumpInfinite {
    0% {
        margin-top: 0;
    }

    50% {
        margin-top: 20px;
    }

    100% {
        margin-top: 0;
    }
}

.down-arrow:hover {
    cursor: pointer;
    color: rgb(0, 0, 0);
    background-color: rgb(0, 0, 0);
}